import React from 'react';
import '../../../../src/styles/views/_resume.scss';
import { Grid, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@material-ui/core';
import { useSelector } from 'react-redux';
import { imageSelector } from 'app/redux/selectors/StaffSelector';
import moment from 'moment';
import { GENDER, RELATIONSHIP } from 'app/constants/staffConstant';
import MaterialTable from 'material-table';
import { getAllFamilyMember } from 'app/redux/selectors/FamilySelector';

const Resume = (props) => {
    const { t, item } = props;

    return (
        <>
            <div className="resume-wrapper">
                <Grid container spacing={2} justify="space-between">
                    <Grid item md={4}>
                        <div className="resume-image">
                            <img alt="avatar" src={item?.image || "/assets/images/avatar.jpg"} />
                        </div>

                    </Grid>
                    <Grid item md={8}>
                        <h3 className="resume-sub-heading">Cng ha x hi ch ngha vit nam</h3>
                        <h4 className="resume-sub-heading heading-underline">c lp - T do - Hnh phc</h4>
                        <h3 className="resume-heading">S yu l lch</h3>
                        <h4 className="resume-sub-heading">T thut</h4>
                    </Grid>
                </Grid>
                <div className="resume-body">
                    <div className="resume-information">
                        <h4 className='information-heading'>I.Thng tin bn thn</h4>
                        <div className="information-details">
                            <Grid container spacing={2}>
                                <Grid item md={8} lg={8} sm={8} className="flex"><span className="detail-tittle">1. H v tn nhn vin:</span><span className="detail-content underline-dashed">{item?.name}</span></Grid>
                                <Grid item md={4} lg={4} sm={4} className="flex"><span className="detail-tittle">2. Gii tnh:</span><span className="detail-content underline-dashed">{GENDER[item?.gender]?.name}</span></Grid>
                            </Grid>
                            <Grid container spacing={2}><Grid item md={12} lg={12} sm={12} className="flex"><span className="detail-tittle">3. Ngy sinh:</span><span className="detail-content underline-dashed">{moment(new Date(item?.dateOfBirth)).format("DD/MM/YYYY")}</span></Grid></Grid>
                            <Grid container spacing={2}><Grid item md={12} lg={12} sm={12} className="flex"><span className="detail-tittle">4. H khu thng tr:</span><span className="detail-content underline-dashed">{item?.address}</span></Grid></Grid>
                            <Grid container spacing={2}><Grid item md={12} lg={12} sm={12} className="flex"><span className="detail-tittle">5. in thoi lin lc: :</span><span className="detail-content underline-dashed">{item?.phone}</span></Grid></Grid>
                            <Grid container spacing={2}>
                                <Grid item md={6} lg={6} sm={6} className="flex"><span className="detail-tittle">6. Dn tc:</span><span className="detail-content underline-dashed">{item?.ethnic}</span></Grid>
                                <Grid item md={6} lg={6} sm={6} className="flex"><span className="detail-tittle">7. Tn gio:</span><span className="detail-content underline-dashed">{item?.religion}</span></Grid>
                            </Grid>
                            <Grid container spacing={2}>
                                <Grid item md={6} lg={6} sm={6} className="flex"><span className="detail-tittle">8. Cn cc cng dn:</span><span className="detail-content underline-dashed">{item?.citizenIdentificationNumber}</span></Grid>
                                <Grid item md={3} lg={3} sm={3} className="flex"><span className="detail-tittle">9. Ngy cp:</span><span className="detail-content underline-dashed">{moment(new Date(item?.dateOfIssuanceCard)).format("DD/MM/YYYY")}</span></Grid>
                                <Grid item md={3} lg={3} sm={3} className="flex"><span className="detail-tittle">10. Ni cp :</span><span className="detail-content underline-dashed">{item?.placeOfIssueCard}</span></Grid>
                            </Grid>

                        </div>
                    </div>
                    <div className="resume-family">
                        <h4 className="family-heading">II. Thng tin gia nh</h4>
                        <div className="family-detail-table">
                            <TableContainer>
                                <Table border="1">
                                    <TableHead>
                                        <TableRow>
                                            <TableCell align="center" width="6%" className="table-head">
                                                STT
                                            </TableCell>
                                            <TableCell align="center" width="20%" className="table-head">
                                                {t('staff.family.name')}
                                            </TableCell>
                                            <TableCell align="center" width="12%" className="table-head">
                                                {t('staff.family.dateOfBirth')}
                                            </TableCell>
                                            <TableCell align="center" width="12%" className="table-head">
                                                {t('staff.family.relationShip')}
                                            </TableCell>
                                            <TableCell align="center" width="14%" className="table-head">
                                                {t('staff.family.phoneNumber')}
                                            </TableCell>
                                            <TableCell align="center" width="16%" className="table-head">
                                                {t('CCCD')}
                                            </TableCell>
                                            <TableCell align="center" width="20%" className="table-head">
                                                {t('staff.family.address')}
                                            </TableCell>
                                        </TableRow>
                                    </TableHead>
                                    <TableBody>
                                        {item?.employeeFamilyDtos?.length > 0 ? (
                                            item?.employeeFamilyDtos?.map((family, index) => (
                                                <TableRow key={family.id}>
                                                    <TableCell align="center" className="table-cell">
                                                        {index + 1}
                                                    </TableCell>
                                                    <TableCell align="left" className="table-cell">
                                                        {family.name}
                                                    </TableCell>
                                                    <TableCell align="center" className="table-cell">
                                                        {moment(new Date(family?.dateOfBirth)).format("DD/MM/YYYY")}
                                                    </TableCell>
                                                    <TableCell align="center" className="table-cell">
                                                        {RELATIONSHIP[family?.relationShip]?.name}
                                                    </TableCell>
                                                    <TableCell align="center" className="table-cell">
                                                        {family.phoneNumber}
                                                    </TableCell>
                                                    <TableCell align="center" className="table-cell">
                                                        {family.citizenIdentificationNumber}
                                                    </TableCell>
                                                    <TableCell align="left" className="table-cell">
                                                        {family.address}
                                                    </TableCell>
                                                </TableRow>
                                            ))
                                        ) : (
                                            <TableRow>
                                                <TableCell className="table-cell"></TableCell>
                                                <TableCell className="table-cell"></TableCell>
                                                <TableCell className="table-cell"></TableCell>
                                                <TableCell className="table-cell"></TableCell>
                                                <TableCell className="table-cell"></TableCell>
                                                <TableCell className="table-cell"></TableCell>
                                                <TableCell className="table-cell"></TableCell>
                                            </TableRow>
                                        )}
                                    </TableBody>
                                </Table>
                            </TableContainer>


                        </div>
                    </div>
                    <div className="resume-reassurance">
                        <h4 className="reassurance-heading">III. LI CAM OAN</h4>
                        <p>Ti xin cam oan bn khai s yu l lch trn l ng s tht, nu c iu g khng ng ti xin chu trch nhim trc php lut v li khai ca mnh.</p>
                    </div>
                </div>
                <div className="resume-footer">
                    <div className="footer-container">
                        <div className="resume-date">
                            <span className="reassurance-place">{item?.address}</span>,
                            <span className="reassurance-day"><span className="date-tittle">ngy</span><span className="date-content underline-dashed">{moment(new Date(item?.submitDay)).format("DD/MM/YYYY").split("/")[0]}</span></span>
                            <span className="reassurance-month"><span className="date-tittle">thng</span><span className="date-content underline-dashed">{moment(new Date(item?.submitDay)).format("DD/MM/YYYY").split("/")[1]}</span></span>
                            <span className="reassurance-year"><span className="date-tittle">nm</span><span className="date-content underline-dashed">{moment(new Date(item?.submitDay)).format("DD/MM/YYYY").split("/")[2]}</span></span>

                        </div>
                        <h4 className="footer-label">Ngi khai</h4>
                        <span className="footer-sub-label">(k v ghi r h tn)</span>
                        <div className="footer-signature">{item?.name}</div>
                    </div>
                </div>

            </div>
        </>
    );
}
export default Resume;